'use client'

import { useState } from 'react'

interface FooterProps {
  activeTab: string
  onTabChange: (tabId: string) => void
}

const tabs = [
  { id: 'home', label: 'Home' },
  { id: 'map', label: 'Map' },
  { id: 'events', label: 'Events' },
  { id: 'clubs', label: 'Clubs' },
  { id: 'profile', label: 'Profile' },
  { id: 'admin', label: 'Admin' },
]

export default function Footer({ activeTab, onTabChange }: FooterProps) {
  return (
    <footer className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-300 shadow-inner flex justify-around py-2 text-sm font-semibold text-gray-700">
      {tabs.map((tab) => (
        <button
          key={tab.id}
          onClick={() => onTabChange(tab.id)}
          className={`flex-1 text-center py-2 hover:bg-gray-100 transition-colors ${
            activeTab === tab.id ? 'text-[#c94a1f] border-t-2 border-[#c94a1f]' : ''
          }`}
          aria-current={activeTab === tab.id ? 'page' : undefined}
        >
          {tab.label}
        </button>
      ))}
    </footer>
  )
}
